<template name="material">
	<view>
		<cu-custom :bgColor="NavBarColor" :isBack="false" backRouterName="index">
			<block slot="content">资料</block>
		</cu-custom>
		<scroll-view>
			<!-- 常用服务 -->
			<view class="cu-bar bg-white solid-bottom" :style="[{animation: 'show 0.5s 1'}]">
				<view class="action">
					<text class='cuIcon-title text-blue'></text>基础资料
				</view>
			</view>
				
			<view class="cu-list grid col-4 text-sm">
				<view class="cu-item animation-slide-bottom" :style="[{animationDelay: (index + 1)*0.05 + 's'}]" v-for="(item,index) in usList" :key="index" @tap="goPage(item.page)">
					<view class="padding text-center">
						<image :src="item.icon" style="width:28px;height:28px;">
							<view class="cu-tag badge margin-top-sm" style="margin-left:1.2em" v-if="getTtemDotInfo(item)">
							   <block v-if="getTtemDotInfo(item)">{{getTtemDotInfo(item)}}</block>
							</view>
						</image>
						<view class="margin-top-xs">{{item.title}}</view>
					</view>
				</view>
			</view>
		</scroll-view>
		<view class="cu-tabbar-height margin-top"></view>
	</view>
</template>

<script>
	import { material,os } from '@/common/util/work.js'
	import socket from '@/common/js-sdk/socket/socket.js'
	export default {
		name: 'material',
		props:{
			cur:String,
		},
		priorityOrder:["商品管理", "仓库管理", "角色管理"],
		watch: {
			cur: {
				immediate: true,
				handler:function(val,oldVal){
					console.log('cur',val,oldVal)
				    this.initMaterialList()
				},
			},
		},
		data() {
			return {
			 swiperList: [
				  {id:1,type: 'image',url: '/static/banner/banner1.png', link: ''},
				  {id:2,type: 'image',url: '/static/banner/banner2.jpg', link: ''},
				  {id:3,type: 'image',url: '/static/banner/banner3.jpg', link: ''},
				  {id:4,type: 'image',url: '/static/banner/banner4.jpg', link: ''},
				],
				middleApps: [
				  {icon: 'line2_icon1.png', title: '审批', 'text': '个人审批'},
				  {icon: 'line2_icon2.png', title: '审批稿', 'text': '审批草稿箱'},
				],
				usList:[],
				msgCount:0,
				dot:{
				  mailHome:false
				}
			}
		},
		methods: {
			initMaterialList(){
				let permissionList=uni.getStorageSync("permissionList");
				let data=[]
				let materialList=[]
				let o1 = {}
				o1.url = "/material/material"
				o1.page = "goods"
				o1.title = "商品管理"
				materialList.push(o1)
				let o2 = {}
				o2.url = "/material/material_category"
				o2.page = "gaccount"
				o2.title = "商品类别"
				materialList.push(o2)
				let o3 = {}
				o3.url = "/system/unit"
				o3.page = "unites"
				o3.title = "计量单位	"
				materialList.push(o3)
				let o4 = {}
				o4.url = "/material/material_attribute"
				o4.page = "multiAttribute"
				o4.title = "多属性"
				materialList.push(o4)
				let o5 = {}
				o5.url = "/system/vendor"
				o5.page = "vendors"
				o5.title = "供应商管理"
				materialList.push(o5)
				let o6 = {}
				o6.url = "/system/customer"
				o6.page = "customers"
				o6.title = "客户管理"
				materialList.push(o6)
				let o7 = {}
				o7.url = "/system/member"
				o7.page = "members"
				o7.title = "会员管理"
				materialList.push(o7)
				let o8 = {}
				o8.url = "/system/depot"
				o8.page = "depot"
				o8.title = "仓库管理"
				materialList.push(o8)
				let o9 = {}
				o9.url = "/system/in_out_item"
				o9.page = "inOutItemList"
				o9.title = "收支项目"
				materialList.push(o9)
				let o10 = {}
				o10.url = "/system/account"
				o10.page = "accounts"
				o10.title = "账户管理"
				materialList.push(o10)
				let o11 = {}
				o11.url = "/system/person"
				o11.page = "person"
				o11.title = "经手人管理"
				materialList.push(o11)
				let o12 = {}
				o12.url = "/system/role"
				o12.page = "role"
				o12.title = "角色管理"
				materialList.push(o12)	 
				if(permissionList && permissionList.length > 0){
				     for(var i = 0; i < permissionList.length; i++) {
						 if(permissionList[i].children && permissionList[i].children.length > 0){
							 for(var k = 0; k < permissionList[i].children.length; k++) {
								 for(var j = 0; j < materialList.length; j++) {
								  if(permissionList[i].children[k].url == materialList[j].url) {
								   let m ={}
								   m.title=materialList[j].title
								   m.icon ="/static/home/128/wendang.png"
								   m.description =materialList[j].title
								   m.useCount =1000,
								   m.page =materialList[j].page
								   data.push(m);
								  }
								 }
								 }
						 }
				     }
				    } 
					data.sort((a, b) => a.title.localeCompare(b.title)); // 按标题排序
					this.usList = data;
			},
			initMenu(){
				console.log("-----------home------------")
			    // this.onSocketOpen()
			    // this.onSocketReceive()
			    this.loadCount(0);
			},
			goPage(page){
				if(!page){
					return false;
				}
				if(page==='annotationList'){
				  this.msgCount = 0
				}
				this.dot[page]=false
				this.$Router.push({name: page})
			},
			// 启动webSocket
			onSocketOpen() {
				socket.init('websocket');
			},
			onSocketReceive() {
				var _this=this
				socket.acceptMessage = function(res){
					// console.log("页面收到的消息", res);
					if(res.cmd == "topic"){
					  //系统通知
					  _this.loadCount('1')
					}else if(res.cmd == "user"){
					  //用户消息
					  _this.loadCount('2')
					} else if(res.cmd == 'email'){
					 //邮件消息
					  _this.loadEmailCount()
					}
				}
			},
			loadCount(flag){
				console.log("loadCount::flag",flag)
				let url = '/sys/annountCement/listByUser';
				this.$http.get(url).then(res=>{
					console.log("res::",res)
				  if(res.data.success){
					let msg1Count = res.data.result.anntMsgTotal;
					let msg2Count = res.data.result.sysMsgTotal;
					this.msgCount = msg1Count + msg2Count
					console.log("this.msgCount",this.msgCount)
				  }
				})
			},
			loadEmailCount(){
				this.dot.mailHome = true
			},
			getTtemDotInfo(item){
				if(item.page==='annotationList' && this.msgCount>0){
				  return this.msgCount
				}
				return '';
			}
		}
	}
</script>

<style scoped>
	.cu-list.grid>.cu-item {
	  padding: 0px 0px; 
	}
    .line2-icon {
	  width: 60px;
	  height: 60px;
    }
</style>
